/* all the styles defined for used modules */

body {
    margin: 0;
    color: $font-color;
    font-family: $font-style;
    font-size: 1.1em;
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3 {
    font-weight: 600;
    color: $heading-font-color;
    font-family: $heading-font;
    line-height: 1.5;
}

h1 {
    color: $main;
    font-size: 2.5em;

    a,
    a:visited {
        text-decoration: none;
        color: $main;
    }
}

h2 {
    font-size: 2em;
}

a,
a:visited {
    color: $link-color;
    text-decoration: underline;
    font-weight: bold;
}

a:hover {
    color: $link-hover-color;
    text-decoration: underline;
}

header {
    background: $header;
    padding: 0px 15px;
    text-align: center;
    margin: 50px 0 0;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

main {
    padding: 0 15px;
    max-width: $content-width;
    margin: 0 auto;
}

time {
    color: #898989;
}

.container {
    max-width: $content-width;
    margin: 0 auto;
}

aside {
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    width: 100%;
    z-index: 2;
    border-bottom: 1px solid lighten($light, 30%);
    box-shadow: 0px 1px 1px RGBA(4, 25, 54, 0.1);

    nav {
        float: left;
        max-width: 800px;
        margin: 0 auto;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                float: left;
                position: relative;

                a {
                    text-decoration: none;
                    display: block;
                    padding: 15px;
                    font-family: 'Muli', sans-serif;
                    line-height: 20px;
                    margin-bottom: -1px;
                    box-shadow: 0;

                    &:hover,
                    &:active {
                        background: $header;
                        text-decoration: none;
                        color: $main;
                    }
                }
            }
        }

        &:after {
            content: '';
            display: table;
            clear: both;
        }
    }
}

footer {
    text-align: center;
    padding: 40px;
}

.hover-underline {
    position: relative;
}

.hover-underline:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -2px;
    left: 0;
    background-color: #333333;
    visibility: hidden;
    transform: scaleX(0);
    transition: transform 200ms ease-in-out;
}

.hover-underline:hover:after {
    visibility: visible;
    transform: scaleX(1);
}

.logo {
    width: 100px;
    height: 100px;
    filter: none;
    cursor: pointer;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: 10px;
    border: .08em solid #ffffff !important;
    user-select: none;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -moz-transition: -moz-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -o-transition: -o-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -ms-transition: -ms-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
  }
  .logo:hover {
    -webkit-transform: rotateZ(360deg);
    -moz-transform: rotateZ(360deg);
    -o-transform: rotateZ(360deg);
    -ms-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }